<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data() {
    return {
      url:''
    }
  },
  methods: {
    getClearCacheData: function(params) {
      api.clearCache(params).then(res => {
        // console.log(res)
        if(res.code == 0) {
          this.$Message.success({
            content: res.msg,
            duration: 3
          })
        } else {
          this.$Message.error({
            content: res.msg,
            duration: 5
          })
        }
      }).catch(err => {
        this.$Notice.error({
          desc: err,
        })
      })
    },
    clearUrl: function() {
      let reg1 = /^http:\/\//i,
          reg2 = /^https:\/\//i,
          falg = this.url.trim().match(reg1) || this.url.trim().match(reg2);

      if(this.url.trim() === '') {
        this.$Message.warning({
          content: '请输入需清除缓存的url',
          duration: 3
        })
      } else if (!falg) {
        this.$Message.warning({
          content: '请输入正确的url',
          duration: 3
        })
      } else {
        const token = getToken(),
              url = this.url,
              params = {"token": token, "type": 2, "url": url};

        this.getClearCacheData(params)
      }
    },
    clearMUrl: function() {
      const token = getToken(),
            params = {"token": token, "type": 1};

      this.getClearCacheData(params)
    },
    clearPcUrl: function() {
      const token = getToken(),
            params = {"token": token, "type": 3};

      this.getClearCacheData(params)
    }
  }
}
</script>
<template>
  <div>
    <h3><Icon type="md-trash" size=24 />缓存清理</h3>
    <Button @click="clearMUrl" type="success" ghost>清理M全部</Button>
    <Button @click="clearPcUrl" type="warning" ghost>清理PC全部</Button>
    <Input v-model="url" search enter-button="清理指定URL" @on-search="clearUrl" placeholder="请输入指定url..." />
  </div>
</template>
<style lang="scss" scoped>
  h3 {
    line-height: 40px;
    font-size: 18px;
    i {
      margin: -2px 4px 0 0;
    }
  }
  .ivu-btn {
    margin: 20px;
  }
  .ivu-input-wrapper {
    margin-left: 20px;
    width: 40%;
  }
</style>